<template>
	<div class="mingyan">
		<quoteGrid @quoteDelete="dequote" :quotes='quotes'></quoteGrid>
		<newQuote @quoteAdd="newquote"></newQuote>
	</div>
</template>

<script>
	import quoteGrid from '../components/mingyan/quoteGrid.vue'
	import newQuote from '../components/mingyan/newQuote.vue'
	export default{
		data(){
			return {
				quotes:['明天你好'],
				maxQuotes: 10
			}
		},
		components:{
			quoteGrid,
			newQuote
		},
		methods:{
			newquote(quote){
				this.quotes.push(quote)
			},
			dequote(index){
				this.quotes.splice(index)
			}
		}

	}
</script>