<template>
	<div class="row">
		<quote @click.native="deletequote(index)" v-for="(item, index) in quotes">{{item}}</quote>
	</div>
</template>


<script>
	import quote from './quote.vue'
	export default{
		data(){
			return{

			}
		},
		props:{
			quotes:{
				type:Array
			}
		},
		components:{
			quote,
		},
		methods:{
			deletequote(index){
				this.$emit('quoteDelete', index)
			}
		}
	}
</script>