<template>
	<div class="row">
		<form>
			<div>
				<label>Annie</label>
				<textarea class="ree" rows="3" cols="20" v-model="quote"></textarea>
			</div>
			<div>
				<button @click="createNew()">Add</button>
			</div>
		</form>
	</div>
</template>


<script>
	export default{
		data(){
			return{
				quote:''
			}
		},
		methods:{
			createNew(){
				this.$emit('quoteAdd',this.quote);
				this.quote = ""
			}
		}
	}
</script>

<style lang="scss">
	.ree{
		background: red;
	}
</style>